<template>
<slot v-if="loading">
</slot>
</template>

<script>
export default {
    name: 'App',
    data(){
        return {
            loading : false,
            permissions : {},
        }
    },
    inject: ['show', 'hide'],
    mounted() {

        let token = localStorage.getItem('token');
        if (!token) {
            this.loading =  true;
            this.$router.push('/login');
        } else {
            this.$store.dispatch('users/getAdminDetails', token).then(result=>{
                if(result.auth){
                    this.loading = true; 
                    this.permissions = result.permissions;
                    
                }
            }).catch((err) => {
                this.show(err.message, 'error', 3000)
                this.$router.push('/login');
            })
        }
    },
    created() {
        document.title = "PickNDeal";
    },
}
</script>

