<template>
    
        <a class="btn btn-info btn-xs" @click="toggleModal({'status':true,'form':obj})">View</a>
    
    </template>
    
    <script>
    import { mapActions, mapState } from "vuex";
    
    export default {
        computed:{
            ...mapState('modal',["status"])
        },
        props:{
            id: Number,
            obj: Object,
        },
        methods:{
            ...mapActions('modal',["toggleModal"]),
            ...mapActions('helpers',["deleteForm"]),
            
        },
        computed:{
           ...mapState('users', {
                  pagePermission : (state) =>{ 
                      
                      return state.permission?.services.includes('view')  ? true : false;
                  }
            })
        },
    };
    </script>